body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: white;
}

*:focus {
  outline: none;
}

@font-face {
    font-family: 'Game of Thrones';
    src: url(/glur4k/got-calc/static/media/GameofThrones.cd6001b7.woff2) format('woff2'),
        url(/glur4k/got-calc/static/media/GameofThrones.c1954136.woff) format('woff'),
        url(/glur4k/got-calc/static/media/GameofThrones.508611eb.ttf) format('truetype');
    font-weight: normal;
    font-style: normal;
}

.fullpage-wrapper {
  display: grid;
  grid-template-columns: 50px auto 50px;
  background-color: #1F1F1F;
}

.section {
  grid-column-start: 2;
  grid-column-end: 2;
  text-align: center;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  padding: 0 1rem;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: black;
}

h2 {
  margin-top: 0;
  padding: 1rem;
  font-family: 'Game of Thrones', serif, sans-serif;
  font-weight: 400;
  font-size: 2rem;
  line-height: 3rem;
  letter-spacing: 3px;
}

button {
  border: 2px solid transparent;
  background: white;
  color: black;
  padding: 10px 30px;
  border-radius: 5px;
  font-size: 16px;
  display: block;
  margin: 0 auto 5px;
}

button:hover,
button:focus,
button.active {
  background: black;
  color: white;
  border: 2px solid white;
  cursor: pointer;
}

